<script>
import JsBarcode from 'jsbarcode';
export default {
  props: {
    barCodeMsg: { // 
      type: String,
      default: ''
    },
    displayValue: { // 
      type: Boolean,
      default: false
    },
    width: { // 
      type: Number,
      default: 1
    },
    height: { // 
      type: Number,
      default: 50
    },
    background: { // 
      type: String,
      default: '#FFFFFF'
    },
    lineColor: { // 
      type: String,
      default: '#000000'
    }
  },
  data() {
    return {
      codeMsg: ''
    }
  },
  watch: {
    barCodeMsg(newVal) {
      this.codeMsg = newVal
      this.createCode()
    }
  },
  mounted() {
    this.codeMsg = this.barCodeMsg
    this.createCode()
  },
  render() {
    return <span>
      <svg ref="barCode" style={{height: 0}}></svg>
    </span>
  },
  methods: {
    createCode() {
      let barCodeMsg = this.codeMsg || 'no'
      JsBarcode(this.$refs.barCode, barCodeMsg, 
        {
          displayValue: this.displayValue, // 
          background: this.background, // 
          lineColor: this.lineColor, // 'rgba(0,0,0,1)'
          width: this.width, // 
          height: this.height
        }
      )
    }
  }
}
</script>



