import requestUrl from '@/module/api/buyAndSale'
import BarCode from '@/components/Common/Tools/BarCode'
import exportExcel from '@/utils/exportExcel'
import {objectToArray} from '@/utils/utils'
export default {
  data() {
    return {
      paperNo: '',
      type: '',
      pageNum: 1,
      pageSize: 50000,
      loading: false,
      exportLoading: false,
      orderMsg: {},
      searchContent: {
        barcode: '', // 
        erpGoodsNo: '', // 
        vendorGoodsNo: '' // 
      },
      havePurchaseQty: "",
      row: {},
      orderSumTableData: [],
      orderSumTableColumn: [
        {
          title: '',
          dataIndex: 'vendorGoodsNo',
          minWidth: 100
        },
        {
          title: '',
          dataIndex: 'barcode',
          minWidth: 130
        },
        {
          title: '',
          dataIndex: 'goodsNo',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'goodsName',
          minWidth: 120
        },
        {
          title: '',
          dataIndex: 'unit',
          minWidth: 80
        },
        {
          title: '',
          dataIndex: 'spec',
          minWidth: 80
        },
        {
          title: '',
          dataIndex: 'amount',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'qty',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'packQty',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'price',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'packPrice',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'receiveQty',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'giftQty',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'receivePrice',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'receiveAmount',
          minWidth: 110
        },
        // {
        //   title: '',
        //   dataIndex: 'vendorPrice',
        //   minWidth: 110,
        //   input: this.isInput,
        //   placeholder: ''
        // },
        // {
        //   title: '',
        //   dataIndex: 'vendorQty',
        //   minWidth: 110,
        //   input: this.isInput,
        //   placeholder: ''
        // },
        {
          title: '',
          dataIndex: 'vendorRemark',
          minWidth: 120,
          input: this.isInput,
          placeholder: ''
        }
      ],
      exportConfig: {
        vendorGoodsNo: '',
        barcode: '',
        goodsNo: '',
        goodsName: '',
        unit: '',
        spec: '',
        amount: '',
        qty: '',
        packQty: '',
        price: '',
        packPrice: '',
        totalReceiveQty: '',
        giftQty: '',
        receivePrice: '',
        totalReceiveAmount: '',
        //   vendorPrice: '',
        //   vendorQty: '',
        vendorRemark: ''
      },
      storeGoodsTableColumn: [
        {
          title: '',
          dataIndex: 'pickStoreNo',
          minWidth: 100
        },
        {
          title: '',
          dataIndex: 'vendorGoodsNo',
          minWidth: 100
        },
        {
          title: '',
          dataIndex: 'barcode',
          minWidth: 100
        },
        {
          title: '',
          dataIndex: 'goodsNo',
          minWidth: 90
        },
        {
          title: '',
          dataIndex: 'goodsName',
          minWidth: 120
        },
        {
          title: '',
          dataIndex: 'unit',
          minWidth: 80
        },
        {
          title: '',
          dataIndex: 'spec',
          minWidth: 80
        },
        {
          title: '',
          dataIndex: 'totalQty',
          minWidth: 110
        },
        {
          title: '',
          dataIndex: 'pickQty',
          minWidth: 110
        }
      ],
      exportConfigForStoreGoods: {
        pickStoreNo: '',
        vendorGoodsNo: '',
        barcode: '',
        erpGoodsNo: '',
        goodsName: '',
        unit: '',
        spec: '',
        qty: '',
        pickQty: ''
      }
    }
  },
  components: {
    "bar-code": BarCode
  },
  created() {
    this.paperNo = this.$route.params.id
    this.type = this.$route.params.type
    this.storeNo = this.$route.params.storeNo
    this.havePurchaseQty = this.$route.query.row && this.$route.query.row.havePurchaseQty
    this.row = this.$route.query.row
  },
  async mounted() {
    let orderMsg = await this.getOrderDetail()
    this.orderMsg = {...orderMsg}
    this.paperNo = orderMsg.paperNo // paperNo paperNo
    this.orderSumTableData = orderMsg.listDetail || []
    // this.getOrderSum()
    // if(this.type === '001' && this.orderMsg.havePurchaseQty === 1) {
    //   this.getStoreDetail()
    // }
  },
  methods: {
    /**
     * @description: 
     * @param {*} async
     * @return {*}
     */
    async getOrderDetail() {
      let _this = this
      _this.$loading.show()
      return new Promise((resolve, reject) => {
        // _this.$http.post(`${requestUrl.buyAndSaleOrderDetail}${_this.paperNo}`).then(res => {
        _this.$http.post(requestUrl.orderDetail, { paperNo: _this.paperNo, storeNo: _this.storeNo, ...this.row, selectStoreNeed: this.havePurchaseQty === 1 ? 1 : null, selectReceive: 1 }).then(res => {
          _this.$loading.hide()
          if(res.code === 0) {
            let storeGoodsTableDataList = res.data && res.data.listOrderPickDetail && res.data.listOrderPickDetail.length ? res.data.listOrderPickDetail : []
            this.storeGoodsTableData = storeGoodsTableDataList
            this.storeGoodsTableDataTemp = storeGoodsTableDataList
            resolve(res.data)
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          _this.$loading.hide()
          this.$tip.notify(e.message || '', 'error', '')
        })
      })
    },
    /**
     * @description: 
     */
    getOrderSum() {
      this.loading = true
      this.$http.post(requestUrl.orderDetail, {...this.searchContent, paperNo: this.paperNo, pageNum: this.pageNum, pageSize: this.pageSize, storeNo: this.storeNo}).then(res => {
        this.loading = false
        if(res.code === 0) {
          let tableDataList = res.data && res.data.listDetail && res.data.listDetail.length ? res.data.listDetail : []
          this.orderSumTableData = tableDataList
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.loading = false
        this.$tip.notify(e.message || '', 'error', '')
      })
    },
    exportHandel() {
      this.exportLoading = true
      this.$http.post(requestUrl.orderDetail, {...this.searchContent, paperNo: this.paperNo, pageNum: this.pageNum, pageSize: this.pageSize, storeNo: this.storeNo, selectReceive: 1}).then(res => {
        this.exportLoading = false
        if(res.code === 0) {
          let tableDataList = res.data && res.data.listDetail && res.data.listDetail.length ? res.data.listDetail : []
          const tHeader = objectToArray(this.exportConfig, false)
          const filterVal = objectToArray(this.exportConfig, true)
          exportExcel.ExportJsonToExcel({
            header: tHeader,
            filterVal,
            data: tableDataList,
            filename: `${this.paperNo}-`
          })
          return
        }
        throw new Error(res.msg)
      }).catch(e => {
        this.exportLoading = false
        this.$tip.notify(e.message || '', 'error', '')
      })
    },
    isInput() {
      return this.type === '002' ? true : false
    }
  }
}



